@import "../../../../styles/share.scss";
.wallet {
    //min-width: 80px;
    //margin: 0 16px 0 12px;
    @include flex();
    @include controlbg();
    //padding: 10px 14px 10px 10px;
    //box-sizing: border-box;
    //background: #1B222B;
    padding: 0;
    background-color: transparent;
    .addr {
        font-size: 16px;
        //padding: 0 12px 0 8px;
        line-height: 1;
        color: #fff;
    }
    button {
        display: flex !important;
        align-items: center;
        .dats-icon {
            margin-left: 6px;
        }
    }
    &.Unconnect,
    &.Errormsg {
        //padding-left: 12px;
        //padding-right: 12px;
        justify-content: space-around;
        .account-info {
            background: $primaryColor;
            //display: block;
            font-size: 16px;
        }
    }
    &.Errormsg {
        button {
            color: $downColor;
        }
    }
    &.Unconnect {
        button {
            //color: #9488f0;
            color: #fff;
        }
    }
    .account-info {
        display: flex;
        align-items: center;
        justify-content: center;
        //width: 170px;
        min-width: 50px;
        // height: 44px;
        background: #1B222B;
        border-radius: 6px;
        padding: 0 15px;
        .addr {
            color: #fff;
            margin-right: 6px;
        }
        img {
            margin-left: 6px;
        }
    }
}

.view2 .wallet {
    &.Unconnect {
        .account-info {
            height: 50px;
            margin-right: 0;
            border-radius: 0;
            width: 50px;
            span {
                display: none;
            }
            img {
                margin-left: 0;
            }
        }
        button {
            width: 50px;
            height: 50px;
            text-align: center;
            span {
                display: none;
            }
            background: $primaryColor;
            .dats-icon {
                display: block;
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
    .account-info {
        height: 36px;
        margin-right: 10px;
        .addr-pding {
            display: flex;
            margin-left: 2px;
            margin-right: 8px;
            span {
                color: #fff;
            }
        }
    }
}