@import '../../../styles/share.scss';
.sidebar {
    background-color: $panelColor;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    & > *{
        flex-shrink: 0;
        flex-grow: 0;

        &:last-of-type{
            flex-grow: 1;
        }
    }
    .switch {
        position: relative;
        height: 50px;
        //padding: 6px 16px;
        background: #1E242C;
        @include flex();
        button {
            flex: 1;
            height: 100%;
            color: #fff;
            text-align: center;
            font-weight: bold;
            font-size: 16px;
            z-index: 2;
            cursor: pointer;
            //background: #1E242C;
        }
        &::after {
            content: '';
            display: inline-block;
            width: 50%;
            height: 100%;
            background-color: $titleBgColor;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(0, -50%);
            transition: transform linear 100ms;
            will-change: transform;
            z-index: 1;
        }
        &.liqiudity {
            &::after {
                transform: translate(0, -50%);
            }
        }
        &.trade {
            &::after {
                transform: translate(100%, -50%);
            }
        }
    }
    .pool-activity {
        //padding-top: 12px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .pool {
            @include flex(space-between, stretch);
            height: 36px;
            padding: 0 12px 10px 12px;
            border-bottom: 1px solid #4F5A69;
            .title {
                color: #fff;
                font-size: 18px;
                font-weight: 500;
                line-height: 36px;
            }
            .filter {
                @include flex(space-around);
                //font-size: 14px;
                //button {
                //    background-color: steelblue;
                //    border-radius: 3px;
                //    padding: 4px 8px;
                //    text-align: center;
                //    font-size: 14px;
                //    color: #fff;
                //    &+button {
                //        margin-left: 8px;
                //    }
                //    &:hover,
                //    &.active {
                //        background-color: #9488f0;
                //    }
                //}
            }
        }
    }

    .tab-button {
        border-radius: 3px;
        padding: 3px 8px;
        text-align: center;
        font-size: 14px;
        color: rgba(255, 255, 255, 0.7);
        margin-right: 8px;
        border: 1px solid rgba(255, 255, 255, 0.7);
        min-width: 50px;
        &:hover {
            color: #fff;
            border-color: #fff;
        }
        &.active {
            background-color: $primaryColor;
            color: #fff;
            border-color: $primaryColor;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }
}
