@import '../../../../styles/share.scss';
$white:#fff;
$green:#46d7ab;
$red:#ff646d;
.token-list {
    width: 100%;
    //height: 100%;
    //padding: 6px 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-between;

    & > *:last-of-type{
        flex: 1 0 auto;
        height: 0;
        overflow-y: auto;
    }
    &-header {
        padding: 10px 12px;
        display: flex;
        justify-content: space-between;
        span {
            //display: inline-block;
            font-size: 13px;
            box-sizing: border-box;
            padding: 6px 0;
            color: #6d7986;
            display: block;
            white-space: nowrap;

            &:nth-of-type(1) {
                flex-grow: 1;
                flex-shrink: 0;
                text-align: left;
            }
            &:nth-of-type(2) {
                width: 33%;
                flex-grow: 0;
                flex-shrink: 0;
                text-align: center;
            }
            &:nth-of-type(3) {
                width: 25%;
                flex-grow: 0;
                flex-shrink: 0;
                text-align: right;
            }
        }
        //&::after {
        //    content: '';
        //    display: inline-block;
        //    width: 100%;
        //    height: 1px;
        //    background-color: #6d798688;
        //    position: absolute;
        //    bottom: 2px;
        //    left: 0;
        //}
    }
    .simplebar-content-wrapper,
    .m-list__body {
        ul {
            li {
                padding: 8px 12px;
                cursor: pointer;
                @include flex();
                justify-content: space-between;
                &.fade-enter-done {
                    &.add,
                    &.up {
                        animation: AnimateLiAdd 1.2s linear;
                    }
                    &.rm,
                    &.down {
                        animation: AnimateLiRm 1.2s linear;
                    }
                    .col-1 {
                        .sign {
                            &::after,
                            &::before {
                                animation: Scaling 1.2s linear;
                            }
                        }
                    }
                }
                div,
                span {
                    display: inline-block;
                    box-sizing: border-box;
                    color: #fff;
                }
                &.add,
                &.up {
                    &:hover {
                        background-color: #191f27;
                        .amount {
                            .num {
                                color: #46d7ab !important;
                            }
                        }
                        .value {
                            color: #46d7ab !important;
                        }
                    }
                }
                &.rm,
                &.down {
                    &:hover {
                        background-color: #191f27;
                        .amount {
                            .num {
                                color: #ff646d !important;
                            }
                        }
                        .value {
                            color: #ff646d !important;
                        }
                    }
                }
                .col-1,
                .col-2,
                .col-3 {
                    @include flex(flex-start);
                    font-size: 13px;
                }
                .col-1 {
                    flex-grow: 1;
                    .sign {
                        position: relative;
                        width: 12px;
                        height: 100%;
                        margin-right: 6px;
                        &.add {
                            &::before {
                                content: "";
                                position: absolute;
                                background-color: #46d7ab;
                                width: 12px;
                                height: 2px;
                                top: -1px
                            }
                            &::after {
                                content: "";
                                position: absolute;
                                background-color: #46d7ab;
                                height: 12px;
                                width: 2px;
                                top: -6px;
                                left: 5px;
                            }
                        }
                        &.rm {
                            &::before {
                                content: "";
                                position: absolute;
                                background-color: #ff646d;
                                width: 12px;
                                height: 2px;
                                top: -1px;
                            }
                        }
                    }
                    .amount {
                        flex: 1;
                        display: flex;
                        flex-direction: column;
                        margin-left: 8px;
                        &>div {
                            //display: flex;
                            justify-content: center;
                            width: 100%;
                            span {
                                &:nth-of-type(2) {
                                    color: #6d7986;
                                    padding-left: 5px;
                                }
                            }
                            &.from-pair {
                                margin-bottom: 6px;
                            }
                        }
                    }
                }
                .col-2 {
                    justify-content: center;
                    width: 33%;
                    flex-grow: 0;
                    flex-shrink: 0;
                    .value {
                        span {
                            color: #6d7986;
                            padding-left: 4px;
                        }
                    }
                }
                .col-3 {
                    flex-grow: 0;
                    flex-shrink: 0;
                    width: 25%;
                    justify-content: flex-end;
                    color: #6d7986;
                }
            }
        }
    }
    .m-list__body {
        ul {
            li {
                padding: 10px;
                .col-1,
                .col-2,
                .col-3 {
                    font-size: 14px;
                }
            }
        }
    }
}

.view2 {
    .list-header span {
        font-size: 14px !important;
    }
}

@keyframes Scaling {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes AnimateLiAdd {
    0% {
        opacity: 0;
        height: 0;
        color: $white;
    }
    25% {
        opacity: 0;
        height: 54px; //height of row tradingHistorya and poolActivity
    }
    30% {
        opacity: 1;
    }
    50% {
        color: $white;
    }
    75% {
        color: $green;
    }
    100% {
        color: $white;
    }
}

@keyframes AnimateLiRm {
    0% {
        opacity: 0;
        height: 0;
        color: $white;
    }
    25% {
        opacity: 0;
        height: 54px; //height of row tradingHistorya and poolActivity
    }
    30% {
        opacity: 1;
    }
    50% {
        color: $white;
    }
    75% {
        color: $red;
    }
    100% {
        color: $white;
    }
}
