 .gLoader {
     position: fixed;
     width: 90px;
     height: 90px;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
     display: flex;
     justify-content: center;
     align-items: center;
     img {
         display: inline-block;
         width: 100%;
         height: auto;
         border: none;
         outline: 0;
     }
 }