.swap-list {
    width: 100%;
    box-sizing: border-box;
    flex-grow: 1;
    flex-shrink: 1;
    .swap-header {
        display: flex;
    }
    .swap-header {
        padding: 14px 0;
        position: relative;
        &.isMobile {
            padding: 8px 0;
            &>div {
                font-size: 14px;
            }
        }
        &>div {
            flex: 1;
            min-width: 20%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 13px;
            padding: 6px;
            color: #6d7986;
            box-sizing: border-box;
            &:nth-of-type(1) {
                width: 35%;
            }
        }
        //&::after {
        //    content: '';
        //    display: inline-block;
        //    width: 100%;
        //    height: 1px;
        //    background-color: #6d798688;
        //    position: absolute;
        //    bottom: 2px;
        //    left: 0;
        //}
    }
    .simplebar-wrapper,
    .m-swap-body {
        .swapbody__item {
            display: flex;
            align-items: center;
            width: 100%;
            padding: 8px 12px;
            box-sizing: border-box;
            cursor: pointer;
            transition: all ease 100ms;
            overflow: hidden;
            &:hover {
                background-color: #1f262f;
            }
            &.active {
                background-color: #191f27;
                cursor: default;
            }
            a {
                flex: 1;
                display: flex;
                &>div {
                    min-width: 20%;
                    display: flex;
                    align-items: center;
                    flex: 1;
                    height: 36px;
                    padding: 6px 0;
                    span {
                        display: inline-block;
                        width: 100%;
                        font-size: 13px;
                        text-align: center;
                        color: #fff;
                        &.color2 {
                            color: #ff8a1f;
                        }

                        &:last-of-type{
                            font-size: 12px;
                        }
                    }
                    &.name {
                        width: 35%;
                        align-items: center;
                        .pair {
                            display: flex;
                            align-items: center;
                            padding-left: 8px;
                            button {
                                &:first-of-type{
                                    margin-right: 4px;
                                }
                            }
                            &>div {
                                display: inline-block;
                                margin-left: 8px;
                                span {
                                    display: block;
                                    width: 100%;
                                }
                            }
                        }
                    }
                    &.volume,
                    &.liquidity,
                    &.price {
                        flex-direction: column;
                        justify-content: space-between;
                        &.center {
                            justify-content: center;
                        }
                        span {
                            display: inline-block;
                            width: 100%;
                        }
                    }
                    &.price {
                        .dy {
                            display: flex;
                            justify-content: center;
                            img {
                                margin-top: 1px;
                            }
                            i {
                                &.up {
                                    color: #46d7ab;
                                }
                                &.down {
                                    color: #ff646d;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .m-swap-body {
        .swapbody__item {
            padding: 10px;
            &>div {
                padding: 2px 0;
                span {
                    font-size: 14px;
                }
            }
        }
    }
}

.color3 {
    color: #7b7f84 !important;
}

.view2{
    .swap-list .m-swap-body .swapbody__item{
        padding: 8px 2px;
    }
}
