        @import '../../../styles/share.scss';
        .c-trade {
            display: flex;
            margin-top: $gap;
            .trade-panel {
                flex-grow: 1;
                flex-shrink: 1;
                background: #1f242b;
                padding: 20px 20px 26px 20px;
                box-sizing: border-box;
                h2 {
                    font-weight: normal;
                    font-size: 22px;
                    text-align: center;
                }
                &.v2 {
                    padding: 0;
                }
                .panel-div {
                    flex-grow: 0;
                    flex-shrink: 0;
                    width: 15px;
                    //width: 1px;
                    //background: rgba(255, 255, 255, .05);
                }
                .switch {
                    position: relative;
                    height: 48px;
                    //padding: 6px 16px;
                    @include flex();
                    button {
                        //flex: 1;
                        height: 100%;
                        color: #fff;
                        text-align: center;
                        font-weight: bold;
                        font-size: 16px;
                        z-index: 2;
                        cursor: pointer;
                        width: 50%;
                    }
                    &::after {
                        content: '';
                        display: inline-block;
                        width: 50%;
                        height: 100%;
                        background-color: #4f5a69;
                        position: absolute;
                        top: 50%;
                        left: 0;
                        transform: translate(2%, -50%);
                        transition: transform linear 100ms;
                        will-change: transform;
                        z-index: 1;
                    }
                    &.buy {
                        &::after {
                            transform: translate(0, -50%);
                        }
                    }
                    &.sell {
                        &::after {
                            transform: translate(100%, -50%);
                        }
                    }
                }
            }
            .panel-div {
                flex-grow: 0;
                flex-shrink: 0;
                width: $gap;
                //width: 1px;
                //background: rgba(255, 255, 255, .05);
            }
        }
        
        .c-swap {
            margin-top: $gap;
            .c-formWrapper {
                width: 100%;
                background: #222B36;
                display: flex;
                justify-content: space-between;
                align-items: center;
                box-sizing: border-box;
                padding: 8px 19px 24px;
                .swap-input {
                    width: 45%;
                    // .title {
                    //     font-size: 20px;
                    //     font-weight: 400;
                    //     text-align: center;
                    //     &.buy {
                    //         color: #46D7AB;
                    //     }
                    //     &.sell {
                    //         color: #FF646D;
                    //     }
                    // }
                    .btn {
                        margin-top: 16px;
                        button {
                            .approvaling {
                                display: flex;
                                width: 100%;
                                justify-content: center;
                                align-items: center;
                                &>div {
                                    margin-left: 6px;
                                }
                            }
                        }
                    }
                }
                .devider {
                    width: 2px;
                    height: 260px;
                    background: #3C4550;
                    // margin-top: -50px;
                }
            }
        }