@import '../../../../styles/share.scss';
.c-trade-header {
    padding-top: 5px;
    .title {
        padding: 0 8px;
        color: #fff;
        font-size: 18px;
        font-weight: 500;
        line-height: 36px;
    }
    .amount {
        margin: 12px;
        display: flex;
        p {
            margin: 0;
            &:nth-of-type(1) {
                color: #fff;
                font-size: 32px;
                //font-weight: bold;
                line-height: 1;
                i {
                    font-style: normal;
                    font-weight: normal;
                    opacity: .4;
                }
            }
            &:nth-of-type(2) {
                &.up{
                    color: $upColor;
                }
                &.down{
                    color: $downColor;
                }
                margin-left: 8px;
                line-height: 32px;
            }
        }
    }
    .tab-btns {
        display: flex;
        //margin-top: 10px;
        margin: 24px 0 16px 12px;
        //justify-content: flex-end;
    }
}

