.view2 .c-trade-header{
    padding-top: 0;
    .amount{
        margin: 0;
        padding: 10px 10px;
        border-bottom: 1px solid rgba(255, 255, 255, .1);
        p{
            &:nth-of-type(1) {
                font-size: 28px;
                i{
                    font-size: 20px !important;
                }
            }

            &:nth-of-type(2) {
                font-size: 16px;
                line-height: 32px;
            }
        }
    }
    .tab-btns{
        margin: 10px;
    }

    .c-lineChart-wrapper{
        padding: 0 4px 0 4px;
    }
    .tab-button {
        border-radius: 3px;
        padding: 3px 8px;
        text-align: center;
        font-size: 14px;
        color: rgba(255, 255, 255, 0.7);
        margin-right: 8px;
        border: 1px solid rgba(255, 255, 255, 0.7);
        min-width: 50px;
        &:hover {
            color: #fff;
            border-color: #fff;
        }
        &.active {
            background-color: $primaryColor;
            color: #fff;
            border-color: $primaryColor;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }
}
