.token-skeleton {
    width: 100%;
    height: calc(100vh - 60px);
    overflow: hidden;
    //@include flex();
    flex-direction: column;
    display: flex;
    justify-content: space-between;
    &__center{
        width: 100%;
        margin: $gap 0;
        //background: red;
        @include flex();
        align-items: stretch;
        flex-grow: 1;
        height: 100%;
        flex-shrink: 1;
        overflow: hidden;
        //height: 100%;
        //max-height: 580px;

    }
    &__left,
    &__right,
    &__main {
        transition: all ease-out 300ms;
        color: #fff;
        display: flex;
        flex-direction: column;
    }
    &__left,
    &__right {
        flex-shrink: 0;
        flex-grow: 0;
        box-sizing: border-box;
        position: relative;
        z-index: 2;
        &>button {
            display: inline-block;
            width: 20px;
            height: 46px;
            position: absolute;
            background: #4F5A69;
            transition: rotate ease 100ms;
            z-index: 10;
        }
    }
    &__left {
        margin-right: $gap;
        &>button {
            right: -20px;
            top: 100px;
            border-radius: 0 3px 3px 0
        }
        &.close {
            width: 0;
            opacity: 0.5;
            margin-right: 0;
            &>button {
                transform: rotate(180deg);
                border-radius: 3px 0 0 3px;
            }
        }
        &.open {
            width: 360px;
            opacity: 1;
            &>button {
                transform: rotate(0deg);
            }
        }
    }
    &__right {
        margin-left: $gap;
        &>button {
            left: -20px;
            top: 100px;
            border-radius: 0 3px 3px 0;
            transform: rotate(180deg);
        }
        &.close {
            width: 0;
            opacity: 0.5;
            margin-left: 0;
            &>button {
                border-radius: 3px 0 0 3px;
                transform: rotate(0);
            }
        }
        &.open {
            width: 360px;
            opacity: 1;
            &>button {
                transform: rotate(180deg);
            }
        }
    }
    &__main {
        flex-shrink: 1;
        flex-grow: 1;
        //padding: 0 $gap;
    }
}
