@import '../../styles/share.scss';
.c-navTab {
    width: 100%;
    height: 56px;
    background: #222B36;
    border-top: 1px solid #181c23;
    z-index: 101;
    display: flex;
    padding: 8px 10px;
    box-sizing: border-box;
    align-items: center;
    justify-content: space-between;
    .left {
        display: flex;
        align-items: flex-end;
        cursor: pointer;
        //flex-grow: 1;

        .currency{
            //display: flex;
            flex-shrink: 0;
            flex-grow: 1;

            p {
                &:first-of-type{
                    font-size: 17px;
                    color: #fff;
                    font-weight: 500;
                }

                &:last-of-type{
                    margin-top: 4px;
                    font-size: 12px;
                    color: #fff;
                    white-space: nowrap;
                    display: flex;

                    .dats-icon{
                        margin-left: 6px;
                    }

                    i{
                        display: inline-block;
                        //margin-left: 6px;
                        color: $upColor;

                        img{
                            vertical-align: -2px;
                        }
                    }
                }
            }
        }

        .arrow{
            width: 11px;
            flex-grow: 0;
            flex-shrink: 1;
            margin-left: 10px;
            height: 16px;

            &.active img{
                transform: rotate(0deg);
            }

            img{
                width: 100%;
                transform: rotate(180deg);
            }
        }
    }
    .right {
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        height: 100%;
        button {
            margin-left: 10px;
        }
        .icon-btn {
            height: 38px;
            width: 38px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: #1E242C;
            flex-shrink: 0;
            color: #fff;
            font-size: 16px;
            border-radius: 3px;
            span {
                font-size: 13px;
                margin-top: 4px;
            }
        }
    }
}

.trade-button {
    height: 38px;
    width: 100%;
    background: $upColor;
    color: #fff;
    font-size: 16px;
    border-radius: 3px;
    text-align: center;
    &.sell {
        background: $downColor;
    }
    &.dsb {
        background-color: rgb(64, 68, 79);
        color: rgb(108, 114, 132);
        cursor: not-allowed;
    }
}

.switch-btn {
    position: relative;
    justify-content: space-between;
    min-width: 80px;
    height: 39px;
    border-radius: 19px;
    background: #232a32;
    -webkit-tap-highlight-color: transparent;
    outline: 0;
    span {
        position: relative;
        display: inline-block;
        width: 50%;
        z-index: 2;
        font-size: 13px;
        transition: all 0.2s;
    }
    &.USD {
        span {
            &:nth-of-type(1) {
                color: #fff;
            }
            &:nth-of-type(2) {
                color: #6d7986;
            }
        }
        &::before {
            transform: translateX(0);
        }
    }
    &.ETH {
        span {
            &:nth-of-type(1) {
                color: #6d7986;
            }
            &:nth-of-type(2) {
                color: #fff;
            }
        }
        &::before {
            transform: translate(100%, 0);
        }
    }
    &::before {
        content: "";
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        width: 50%;
        height: 39px;
        border-radius: 19px;
        transition: all .25s;
        background-color: #22d59f;
        transform: translate(0, 0);
    }
}

.search-page{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background: #222B36;
    padding-bottom: 56px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .maket-info{
        flex-shrink: 0;
        flex-grow: 0;
    }

    &-content{
        border-top: 8px solid #171C24;
        flex-grow: 1;
        padding: 0 10px;
    }
}
