   .modal-confirm {
       &__cont {
           display: inline-block;
           width: 420px;
           // min-height: 480px;
           background: #222B36;
           border-radius: 6px;
           box-sizing: border-box;
           .title {
               font-size: 24px;
               font-weight: 400;
               color: #46D7AB;
               text-align: center;
               line-height: 1.35;
               &.hidden {
                   display: none;
               }
           }
           .confirm-btn {
               display: flex;
               justify-content: center;
               margin-top: 34px;
               button {
                   max-width: 300px;
                   height: 50px;
                   &.dismiss {
                       color: #fff;
                       background-color: #C22F50;
                   }
               }
           }
           .swap-confirm__swapping,
           .swap-confirm__cmplete,
           .swap-confirm__fail {
               display: flex;
               flex-direction: column;
               justify-content: space-between;
               align-items: center;
               .c-loader {
                   margin: 60px 0;
                   text-align: center;
                   img {
                       display: inline-block;
                       width: 90px;
                       height: 90px;
                   }
               }
           }
           // confirm
           .swap-confirm__cont {
               font-weight: 400;
               margin: 28px auto;
               .A-B {
                   display: flex;
                   flex-direction: column;
                   align-items: center;
                   .icon-arrow2 {
                       width: 20px !important;
                       height: 16px !important;
                       margin: 8px 0;
                   }
                   .input {
                       display: flex;
                       justify-content: space-between;
                       align-items: center;
                       width: 100%;
                       height: 44px;
                       background: #1E242C;
                       border-radius: 4px;
                       box-sizing: border-box;
                       padding: 0 10px;
                       span {
                           color: #fff;
                           font-size: 20px;
                       }
                       &>div {
                           display: flex;
                           align-items: center;
                           .dats-icon {
                               margin-right: 11px;
                           }
                       }
                   }
               }
               .estimate {
                   font-size: 12px;
                   color: #ccc;
                   margin: 20px 0;
                   padding: 0 10px;
                   line-height: 1.35;
               }
               .impact {
                   padding: 5px 10px;
                   border-radius: 4px;
                   background: #2E3740;
                   p {
                       display: flex;
                       justify-content: space-between;
                       align-items: center;
                       padding: 4px 0;
                       span {
                           font-size: 14px;
                           &:nth-of-type(1) {
                               color: #838A93;
                           }
                           &:nth-of-type(2) {
                               color: #FFFFFF;
                           }
                           &.green {
                               color: #46D7AB !important;
                           }
                           &.red1 {
                               color: #FD4040 !important;
                           }
                           &.yellow2 {
                               color: #F3841E !important;
                           }
                           &.green1 {
                               color: #27AE60 !important;
                           }
                       }
                   }
               }
               .confirm-btn {
                   margin-top: 36px;
                   button {
                       height: 50px;
                   }
               }
           }
           // swapping
           .swap-confirm__swapping {
               .c-loader {
                   margin: 60px 0;
                   text-align: center;
               }
               .swapping-footer {
                   p {
                       color: #9488F0;
                       text-align: center;
                       padding: 2px 0;
                       font-size: 18px;
                   }
               }
           }
           // complete
           .swap-confirm__cmplete {
               a {
                   font-size: 18px;
                   font-weight: 400;
                   color: #766DC0;
                   text-decoration: underline;
               }
           }
           // failure 
           .swap-confirm__fail {
               .c-loader {
                   margin-bottom: 34px;
               }
               a {
                   width: 240px;
                   //    height: 44px;
                   padding: 6px 0;
                   background: #39424E;
                   border-radius: 22px;
                   color: #C22F50;
                   text-align: center;
                   line-height: 22px;
               }
           }
       }
   }