@import "../../../../styles/share.scss";
.wallet-container {
    display: flex;
    align-items: center;
    padding-right: 24px;
    border-radius: 3px;
    &>button {
        margin: 0 10px;
        font-weight: 500;
    }
    .wallet {
        min-width: 80px;
        margin: 0 16px 0 12px;
        @include flex();
        @include controlbg();
        padding: 10px 14px 10px 10px;
        background: #1B222B;
        .addr {
            font-size: 16px;
            padding: 0 12px 0 8px;
            line-height: 1;
            color: #fff;
        }
        &.Unconnect,
        &.Errormsg {
            background: $primaryColor;
            padding-left: 12px;
            padding-right: 12px;
            justify-content: space-around;
            button {
                display: block;
                height: 100%;
                font-size: 16px;
            }
        }
        &.Errormsg {
            button {
                color: $downColor;
            }
        }
        &.Unconnect {
            button {
                //color: #9488f0;
                color: #fff;
            }
        }
        .account-info {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .addr-pding {
                display: flex;
                min-width: 80px;
                padding-left: 4px;
                padding-right: 10px;
                .pding {
                    margin-right: 6px;
                    color: #fff;
                }
            }
        }
    }
}

@media screen and (max-width:$mobile) {
    .wallet-container {
        display: none;
    }
}