@import "../../../../styles/share";

.c-header-search{
  background: #1E242C;
  display: flex;
  height: 38px;
  margin-left: 18px;
  align-items: center;
  color: #fff;
  padding-right: 18px;
  cursor: pointer;

  .dd{
    height: 100%;
    padding-left: 18px;
    padding-right: 30px;
    border-right: 1px solid $panelColor;
    display: flex;
    align-items: center;

    strong{
      font-weight: bold;
      font-size: 18px;
      margin-right: 12px;

      //&:last-of-type{
      //  color: #FF8A1F;
      //  font-weight: 400;
      //}
    }

    img{
      margin-top: 2px;
    }
  }

  input{
    width: 300px;
    padding: 0 18px;
    font-size: 16px;
    height: 100%;
    display: block;
    background: none;
    border: 0;
    color: #fff;
    line-height: 2;

    &:focus{
      outline: 0;
    }

  }
}

.c-header-search-modal{
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  .c-header-search-mask{
    background: rgba(0, 0, 0, .6);
    backdrop-filter: blur(4px);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .c-header-search-content{
    width: 600px;
    height: 80vh;
    //min-height: 235px;
    min-height: 484px;
    max-height: 1020px;
    background: #242b35;
    border: 1px solid $primaryColor;
    position: relative;
    z-index: 2;
    top: 10px;
    left: 424px;
    padding: 0 20px;
    box-sizing: border-box;


  }
}
