.account-modal__cont {
    width: 360px;
    min-height: 280px;
    background: #222B36;
    border-radius: 4px;
    display: inline-block;
    .title {
        color: #46D7AB;
        font-size: 20px;
        text-align: center;
        margin-bottom: 30px;
    }
    .user-addr {
        padding: 12px 20px;
        background: linear-gradient(90deg, #39424E 0%, #2C3848 100%);
        box-shadow: 4px 4px 29px 2px rgba(34, 43, 54, 0.33);
        border-radius: 6px;
        .addr {
            display: flex;
            align-items: center;
            &>span {
                color: #fff;
                font-size: 20px;
                padding: 0 12px;
            }
            .copy {
                color: #46D7AB;
                font-size: 16px;
            }
        }
    }
    .transactions {
        margin-top: 34px;
        .tx-title {
            font-size: 20px;
            line-height: 1.35;
            color: #fff;
            margin-bottom: 12px;
        }
        .tx-list {
            min-height: 40px;
            max-height: 200px;
            overflow-x: hidden;
            overflow-y: auto;
            .tx {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 3px 0;
                .pendding-loader {
                    // margin-left: -2px;
                    img {
                        width: 85%;
                        height: 85%;
                    }
                }
                span {
                    font-size: 14px;
                    color: rgb(108, 114, 132);
                    &.red1 {
                        color: #FF646D;
                    }
                    &.green1 {
                        color: #46D7AB;
                    }
                }
            }
        }
        .no-data {
            font-size: 14px;
            color: rgb(108, 114, 132);
            text-align: center;
        }
    }
}

.view2 {
    .account-modal__cont {
        width: 320px;
        min-height: 240px;
        padding-left: 16px;
        padding-right: 16px;
        padding-bottom: 24px;
        border-radius: 6px;
        .dats-mask__close {
            width: 24px;
            height: 24px;
            right: 16px;
            top: 12px;
        }
        .user-addr {
            .addr {
                &>span {
                    font-size: 18px;
                }
            }
        }
        .tx-list {
            .tx {
                a {
                    font-size: 14px;
                }
            }
        }
    }
}