.searchCurrency{
  display: flex;
  flex-direction: column;
  color: #fff;
  height: 100%;
  padding-top: 10px;
  box-sizing: border-box;

  .top{
    flex-grow: 0;
    flex-shrink: 0;

    .search-row{
      display: flex;
      align-items: center;

      .input{
        height: 44px;
        background: #34495E;
        border-radius: 4px;
        flex-grow: 1;
        padding: 0 15px;
        display: flex;

        input{
          flex-grow: 1;
          margin-right: 15px;
          border: 0;
          background: none;
          color: #fff;
          font-size: 16px;

          &:focus{
            outline: 0;
          }

          &::-webkit-input-placeholder {
            color: #999;
          }
        }

        button{
          img{
            width: 30px;
            height: 30px;
          }
        }

      }

      .right{
        font-size: 14px;
        margin-left: 18px;
      }

      .like-icon{
        cursor: default;
        margin-left: 8px;
        margin-top: 2px;
      }
    }

    .most{
      font-size: 12px;
      color: #BDC3CB;
      padding: 25px 0;
      border-bottom: 1px solid rgba(255, 255, 255, .1);

      button{
        height: 22px;
        font-size: 16px;
        padding: 0 8px;
        margin-left: 10px;
        background: #357CE1;
        color: #fff;
        line-height: 22px;
      }
    }

    .title{
      font-size: 22px;
      font-weight: 500;
      line-height: 24px;
      margin-top: 12px;

      img{
        width: 38px;
        line-height: 24px;
        vertical-align: -10px;
        margin-right: 10px;
      }
    }
  }

  .table-header, .table-row{
    display: flex;
    justify-content: space-between;

    & > div{
      font-size: 13px;
      &:nth-of-type(1), &:nth-of-type(2), &:nth-of-type(3){
        width: 30%;
      }

      &:nth-of-type(4){
        width: 10%;
      }
    }
  }

  .table-header{
    padding: 20px 0;
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    color: #7F8FA6;

  }

  .table-row{
    height: 62px;
    cursor: pointer;

    &:hover{
      background: #191f27;
    }

    &.active{
      background: #12171d !important;
    }

    & > div{
      display: flex;
      align-items: center;
      font-size: 16px;

      &:nth-of-type(1){
        padding-left: 8px;

        img{
          width: 30px;
          margin-right: 10px;
        }
      }

      &:nth-of-type(4){
        padding-right: 8px;
        justify-content: flex-end;
      }
    }


  }

  .bottom{
    //background: blue;
    overflow-y: auto;
    flex: 1 0 auto;
    height: 0;

    //.content{
    //  //margin: 20px;
    //  //background: yellow;
    //  //height: 1000px;
    //
    //
    //}
  }
}

.like-icon{
  cursor: pointer;
}

.view2 .searchCurrency{

  .top {
    .search-row{
      .input{
        height: 38px;
        border-radius: 19px;
        background: #1E242C;
      }
    }
    .most{
      padding: 15px 0;
      button{
        height: 18px;
        font-size: 12px;
        line-height: 18px;
      }
    }
    .title{
      display: none;
    }
  }

  .table-header{
    padding: 15px;
    border-bottom: 0;
  }

  .table-row{
    height: 56px;

    & > div:nth-of-type(1) img {
      width: 26px;
    }
  }

}
