.dats-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    img {
        display: block;
        width: 98%;
        height: 98%;
        outline: 0;
        border: none;
    }
    &.isRadius {
        border-radius: 50%;
    }
    &.unknow {
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.9);
    }
}