@import '../../../styles/share.scss';
.p-mobile-token {
    background: #1e242c;
    //padding-bottom: 56px;
    flex-grow: 1;
    flex-shrink: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .chart-wrapper {
        //transition: all ease 120ms;
        overflow: hidden;
        height: 300px;
        background: #222B36;
        margin-bottom: 10px;
        //background: #232a32;
    }
    .switch {
        position: relative;
        height: 40px;
        padding-bottom: 2px;
        @include flex();
        flex-shrink: 0;
        flex-grow: 0;
        background: #34495E;
        button {
            color: #ccc;
            flex-grow: 1;
            width: 50%;
            height: 100%;
            text-align: center;
            font-size: 16px;
            z-index: 2;
            cursor: pointer;
            -webkit-tap-highlight-color: transparent;
            &.active{
                color: #fff;
                font-weight: bold;
            }
            &.tabswap {
                display: flex;
                justify-content: center;
                align-items: center;
                img {
                    margin-left: 10px;
                }
            }
            &.tabliquidity {
                i {
                    display: inline-block;
                    width: 0;
                    height: 0;
                    border-width: 4px;
                    border-color: transparent #fff #fff transparent;
                    border-style: solid;
                    margin-left: 6px;
                    position: relative;
                    z-index: 10;
                }
                .tips-buttons {
                    min-width: 30px;
                    overflow: hidden;
                    a {
                        display: block;
                        padding: 6px 0;
                        font-size: 14px;
                    }
                }
            }
        }
        &::after {
            content: '';
            display: inline-block;
            width: 17.333333333%;
            height: 2px;
            background-color: #9488f0;
            position: absolute;
            left: 0;
            bottom: 0;
            transform: translate(0, 0);
            transition: transform linear 100ms;
            will-change: transform;
            z-index: 1;
            padding-left: 10%;
            padding-right: 10%;
            box-sizing: border-box;
            margin-left: 8%;
            margin-right: 8%;
        }
        &.swap {
            &::after {
                transform: translate(-9%, 0);
            }
        }
        &.liquidity {
            &::after {
                transform: translate(160%, 0);
            }
        }
        &.trade {
            &::after {
                transform: translate(327%, 0);
            }
        }
    }

    .list-wrapper{
        flex-grow: 1;
        flex-shrink: 1;

        & > div{
            display: flex;
            flex-direction: column;
            height: 100%;

            & > div{
                &:nth-of-type(1){
                    flex-grow: 0;
                    flex-shrink: 1;
                }
                &:nth-of-type(2){
                    flex: 1 0 auto;
                    height: 0;
                    overflow-y: auto;
                }
            }
        }
    }
}

.trade-panel {
    flex-grow: 1;
    flex-shrink: 1;
    //background: #1f242b;
    padding: 20px 10px 26px 10px;
    box-sizing: border-box;
    h2 {
        font-weight: normal;
        font-size: 22px;
        text-align: center;
    }
    .trade-button {
        height: 48px;
        width: 100%;
        background: $upColor;
        margin-top: 24px;
        color: #fff;
        font-size: 18px;
        border-radius: 3px;
        &.sell {
            background: $downColor;
        }
    }
}
