@import "../../styles/share";

.c-footer{
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  //margin: 16px 0;
  width: 100%;
  height: 40px;
  background: $panelColor;
  padding: 0 20px;
  box-sizing: border-box;
  align-items: center;
  color: #E4E4E4;
  flex-shrink: 0;
  flex-grow: 0;

  p{
    height: 30px;
    line-height: 30px;
    //opacity: 0.5;
  }

  & > div{
    display: flex;
  }

  a{
    height: 30px;
    line-height: 30px;
    display: flex;
    justify-items: center;
    align-items: center;
    color: #E4E4E4;
    margin-left: 30px;
    font-size: 13px;

    img{
      margin-right: 8px;
    }

    &:first-of-type{
      margin-left: 0;
    }
  }
}

