@import '../../../../styles/share.scss';
.dats-logo {
    //max-width: 300px;
    //min-width: 368px;
    // width: 368px;
    width: 360px;
    background: $panelColor;
    margin-right: $gap;
    height: 100%;
    display: flex;
    align-items: center;
    transition: width .3s ease-out;

    a {
        @include flex();
        padding-left: 18px;
        justify-content: flex-start;
        img {
            display: inline-block;
            //width: 36px;
            //height: 36px;
            width: 190px;
            border: none;
        }
        .dats-name {
            font-size: 28px;
            line-height: 1.2;
            color: #9488f0;
            margin-left: 30px;
        }
    }
}

