.pendding-loader {
    display: flex;
    justify-content: center;
    align-items: center;
    .loading {
        display: inline-block;
        width: 99%;
        height: 99%;
        outline: 0;
        border: none;
        animation: _rotate linear 1.35s infinite;
    }
}

@keyframes _rotate {
    0% {
        transform: rotate(0);
        opacity: 0.9;
    }
    25% {
        transform: rotate(90deg);
        opacity: 0.925;
    }
    50% {
        transform: rotate(180deg);
        opacity: 0.95;
    }
    75% {
        transform: rotate(270deg);
        opacity: 0.975;
    }
    100% {
        transform: rotate(360deg);
        opacity: 1;
    }
}