@import '../../../styles/share.scss';

.swaptoken-list {
    //background-color: #1e242c;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .swaptoken-tag {
        box-sizing: border-box;
        padding: 16px 8px;
        background: #191f27;
        display: flex;
        align-items: center;
        button {
            display: inline-block;
            padding: 4px 8px;
            color: #fff;
            font-size: 14px;
            font-weight: 500;
            text-align: center;
            background: steelblue;
            border-radius: 3px;
            &+button {
                margin-left: 16px;
            }
            &.active {
                background-color: #9488f0;
            }
        }
    }

    &-bottom{
        background: $panelColor;
        margin-top: $gap;
        flex-grow: 1;

        .title{
            font-size: 18px;
            font-weight: 500;
            line-height: 1.5;
            padding-left: 12px;
            padding-top: 12px;
        }
    }
}
