.t-setting {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 14px;
    &>button {
        display: block;
        width: 100%;
        height: 100%;
        background: transparent url('../../../../assets/images/t_setting.png') no-repeat center/contain;
        border: none;
        outline: 0;
        -webkit-tap-highlight-color: transparent;
        text-indent: -999rem;
    }
    .setting-modal {
        // display: none;
        position: absolute;
        bottom: -228px;
        right: 0;
        width: 280px;
        min-height: 220px;
        background: #2E3740;
        box-shadow: -1px 1px 5px 1px rgba(23, 28, 36, .98);
        box-sizing: border-box;
        padding: 16px 20px;
        z-index: 1000;
        border-radius: 6px;
        .c-row {
            // border-bottom: 2px solid #3A434C;
            .title {
                font-size: 16px;
                color: #fff;
                text-align: left;
                margin: 15px 0;
            }
            .action {
                display: flex;
                .slippage-tag {
                    display: flex;
                    align-items: center;
                    background: #1E242C;
                    margin-right: 9px;
                    button {
                        display: inline-block;
                        width: 40px;
                        height: 30px;
                        border: none;
                        outline: 0;
                        font-size: 12px;
                        font-weight: 300;
                        color: #fff;
                        background: transparent;
                        cursor: pointer;
                        transition: background linear 300ms;
                        &.active {
                            background: #348171;
                        }
                    }
                }
            }
            .d-input {
                min-width: 80px;
                max-width: 140px;
                display: flex;
                align-items: center;
                height: 30px;
                box-sizing: border-box;
                padding: 6px 14px;
                font-size: 16px;
                font-weight: 400;
                background: #1E242C;
                input {
                    display: inline-block;
                    width: 50px;
                    height: 100%;
                    border: none;
                    outline: 0;
                    color: #fff;
                    background: #1E242C;
                }
            }
        }
        &>.devider {
            width: 100%;
            height: 2px !important;
            background-color: #3A434C;
            margin: 16px 0 18px;
        }
    }
}