@import '../../../../styles/share.scss';
.c-home-header {
    height: auto;
    &.zeroHeight {
        height: 0;
    }
    &-inner {
        height: 80px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        z-index: 10;
    }
    .h1 {
        display: flex;
        align-items: center;
    }
    .logo {
        display: block;
        width: 218px;
        margin-right: 15px;
    }
    .nav {
        display: flex;
        gap: 48px;
        margin-top: -15px;
        .item,
        .item a {
            //color: #181D23;
            color: #fff;
            font-size: 16px;
            display: flex;
            align-items: center;
            cursor: pointer;
            text-transform: capitalize;
            img {
                margin-left: 8px;
            }
        }
        button {
            background: #fff;
            height: 45px;
            border-radius: 6px;
            color: $primaryColor;
            font-size: 17px;
            padding: 0 20px;
        }
        .wallet .account-info {
            height: 44px;
            background: #fff;
            span {
                color: #555;
            }
        }
    }
}

