.view2 .c-home-header {
    .c-home-header-inner {
        height: 50px;
    }
    .logo {
        width: 120px;
        margin-right: 10px;
    }
    .menu {
        &-btn {
            width: 18px;
            height: 18px;
            background: url('./images/menu.png') center center / contain no-repeat;
        }
        &.active {
            .menu-btn {
                background-image: url('./images/cross.png');
            }
        }
        .c-dropdown-content {
            width: 100%;
            background: #181D23;
            right: 0;
            top: 50px;
            border-radius: 3px;
            display: flex;
            flex-wrap: wrap;
            padding: 40px 30px;
            .list {
                width: 100%;
                a {
                    display: flex;
                    font-size: 18px;
                    color: #ccc;
                    text-transform: capitalize;
                    height: 40px;
                    line-height: 40px;
                    border-bottom: 1px solid rgba(255, 255, 255, .3);
                    margin-top: 20px;
                    justify-content: space-between;
                    align-items: center;
                    &:first-of-type {
                        margin-top: 0;
                    }
                    &:after {
                        content: '';
                        width: 8px;
                        height: 16px;
                        background: url('./images/arrow-right.png') center center / contain no-repeat;
                    }
                }
            }
