@import "../../styles/share";
.view2 {
    .kline-chart {
        margin-right: 0;
    }
    .kline-wrapper {
        height: 100%;
        padding: 2px 2px 0 2px;
        .kline-center {
            .kline-sidebar {
                margin-left: 0;
                margin-right: 2px;
                width: 30px;
                ul li {
                    width: 30px;
                    height: 30px;
                }
            }
        }
        .kline-topBar {
            margin-left: 0;
            ul {
                margin-bottom: 2px;
                li {
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                    img {
                        width: 17px;
                    }
                }
            }
        }
    }
}

.kline-wrapper {
    width: 100%;
    //resize: vertical;
    //overflow: scroll;
    display: flex;
    //flex: 1 1;
    flex-shrink: 1;
    flex-grow: 1;
    flex-direction: column;
    background-color: $panelColor;
    //justify-self: flex-end;
    padding-top: 8px;
    padding-bottom: 15px;
    position: relative;
    z-index: 0;
    button {
        display: block;
        border-radius: 3px;
        //width: 50px;
        //height: 50px;
        background: #000;
        border: 0;
        cursor: pointer;
        &:focus {
            outline: 0;
        }
    }
    //&.fullscreen{
    //  position: fixed;
    //  top: 0;
    //  left: 0;
    //  z-index: 10000;
    //  width: 100vw;
    //  height: 100vh;
    //  min-height: auto;
    //  resize: none;
    //}
    &.fullscreen {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 10000;
        width: 100vw !important;
        height: 100vh !important;
        min-height: auto;
        resize: none;
        .kline-center {
            min-height: auto;
        }
        //&.landscape{
        //  width: 100vh !important;
        //  height: 100vw !important;
        //  transform: rotate(90deg) translateY(-100vw) !important;
        //  transform-origin: top left;
        //}
    }
    .resize {
        min-width: 20px;
        min-height: 20px;
        //resize:both;
        //background: red;
    }
    .exitFullScreen {
        //width: 70px;
        padding: 0 8px;
        height: 34px;
        background: #e64852;
        position: fixed;
        top: 0;
        right: 0;
        z-index: 100001;
        color: #fff;
        border: 0;
        border-radius: 0;
        img {
            width: 20px;
        }
    }
    .kline-center {
        display: flex;
        width: 100%;
        //min-height: 515px;
        flex-grow: 1;
        flex-shrink: 1;
        //resize: vertical;
        overflow: hidden;
    }
    .loading {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, .4);
        backdrop-filter: blur(4px);
        display: flex;
        align-items: center;
        justify-content: center;
        color: #ccc;
        font-size: 16px;
        z-index: 2;
    }
}

.kline-sidebar {
    width: 34px;
    margin-left: 10px;
    margin-right: 5px;
    ul {
        li {
            cursor: pointer;
            //border: 5px solid transparent;
            width: 34px;
            height: 34px;
            //border-radius: 3px;
            //margin-top: 4px;
            //border: 3px solid transparent;
            padding: 6px;
            box-sizing: border-box;
            &:hover {
                background: #1E242C;
                color: #8e9093;
            }
            img {
                width: 100%;
                height: 100%;
            }
        }
    }
    .div {
        width: 24px;
        height: 1px;
        background-color: #ffffff33;
        margin: 10px auto;
    }
}

.kline-topBar {
    width: 100%;
    //height: 34px;
    flex-shrink: 0;
    flex-grow: 0;
    margin-left: 10px;
    ul {
        display: flex;
        margin-bottom: 5px;
        li {
            cursor: pointer;
            //margin-right: 5px;
            height: 34px;
            width: 34px;
            //padding: 0 5px;
            font-size: 12px;
            //border-radius: 3px;
            line-height: 34px;
            box-sizing: border-box;
            background: #1E242C;
            text-align: center;
            color: #fff;
            &:hover {
                background: #27303b;
            }
            &.active {
                background: $upColor;
            }
            img {
                width: 20px;
                display: block;
                padding-top: 7px;
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
}

.kline-bottomBar {
    width: 100%;
    height: 50px;
    background: blue;
    flex-shrink: 0;
    flex-grow: 0;
}

.kline-chart {
    display: flex;
    flex-grow: 1;
    flex-shrink: 1;
    margin-right: 10px;
}
