@import '../../../styles/share.scss';
.buy-sell__cont {
    box-sizing: border-box;
    padding: 20px 0 30px;
    background: #222B36;
    &-header {
        position: relative;
        font-size: 28px;
        font-weight: 400;
        text-align: center;
        margin-bottom: 30px;
        padding: 0 12px;
        .t-setting {
            position: absolute;
            top: 50%;
            right: 12px;
            transform: translateY(-50%);
            width: 24px;
            height: 24px;
            z-index: 1000;
            .d-input {
                span {
                    color: #fff;
                }
            }
        }
    }
    &-form {
        padding: 0 12px;
        margin-bottom: 20px;
    }
    &-summary {
        transition: all 350ms ease-in;
        // will-change: height;
        border-top: 1px solid #000;
        border-bottom: 1px solid #000;
        box-sizing: content-box;
        overflow: hidden;
        &.hidden {
            height: 0;
            padding: 0 12px;
            border-width: 1px;
        }
        &.show {
            padding: 8px 12px;
            border-width: 2px;
            height: 135px;
            // max-height: 200px;
        }
        ul {
            list-style: none;
            li {
                display: flex;
                justify-content: space-between;
                align-self: center;
                margin: 8px 0;
                font-size: 14px;
                span {
                    // font-size: 18px;
                    &:nth-of-type(1) {
                        color: #838A93;
                    }
                    &:nth-of-type(2) {
                        color: #fff;
                    }
                    &.green {
                        color: #46D7AB !important;
                    }
                    &.red1 {
                        color: #FD4040 !important;
                    }
                    &.yellow2 {
                        color: #F3841E !important;
                    }
                    &.green1 {
                        color: #27AE60 !important;
                    }
                }
            }
        }
    }
    &-footer {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 20 12px 24px;
        button {
            width: 42%;
            height: 50px;
            border-radius: 4px;
            font-size: 18px;
            color: #fff;
            margin-top: 24px;
            &.dsb {
                background-color: #40444f !important;
            }
            &.cancel {
                background-color: #2172e5 !important;
            }
            .approvaling {
                display: flex;
                width: 100%;
                justify-content: center;
                align-items: center;
                &>div {
                    margin-left: 6px;
                }
            }
        }
    }
    &.buy {
        .buy-sell__cont-header {
            color: #46D7AB;
        }
        .buy-sell__cont-footer {
            button {
                background: #46D7AB;
            }
        }
    }
    &.sell {
        .buy-sell__cont-header {
            color: #FF646D;
        }
        .buy-sell__cont-footer {
            button {
                background: #FF646D;
            }
        }
    }
}

.swap-state {
    min-height: 360px;
    padding: 20px 6px 30px;
    .m-title {
        font-size: 20px;
        font-weight: 400;
        color: #46D7AB;
        line-height: 1.25;
        text-align: center;
    }
    .swap-confirm__swapping,
    .swap-confirm__cmplete,
    .swap-confirm__fail {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        .c-loader {
            margin: 60px 0;
            text-align: center;
            img {
                display: inline-block;
                width: 90px;
                height: 90px;
            }
        }
    }
    .swap-confirm__swapping {
        .c-loader {
            margin: 60px 0;
            text-align: center;
        }
        .swapping-footer {
            p {
                color: #9488F0;
                text-align: center;
                padding: 2px 0;
                font-size: 16px;
            }
        }
    }
    .swap-confirm__fail {
        .c-loader {
            margin: 60px auto 45px;
        }
        a {
            color: #FF646D;
            font-size: 16px;
            padding-bottom: 12px;
        }
    }
    .swap-confirm__cmplete {
        a {
            color: #9488F0;
            padding-bottom: 12px;
        }
    }
    &>button {
        display: block;
        width: 50%;
        height: 50px;
        font-size: 16px;
        color: #fff;
        border-radius: 6px;
        margin: 20px auto;
    }
    .dismiss {
        background: #FF646D;
    }
    .complete {
        background: #46D7AB;
    }
}