@import "../../styles/share.scss";
.c-header {
    width: 100%;
    height: 60px;
    @include flex();
    &-right {
        height: 100%;
        display: flex;
        background: $panelColor;
        flex-grow: 1;
        justify-content: space-between;
        align-items: center;
    }
}

.c-header-mobile {
    height: 50px;
    background: #34495E;
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .logo {
        display: block;
        width: 40%;
        max-width: 140px;
        margin-left: 10px;
        img {
            width: 100%;
        }
    }
}